import React, { Component } from "react";
import { Icon } from 'antd';
import CardItem from "../../components/cardItem/cardItem";
import Header from '../../components/head/head'
import api from "../../api/index";
import "./topic.scss";

class Topic extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
      commentsAmount:0,
    };
  }
  componentDidMount() {

    api.getArtList().then(res => {
      this.setState({ data: res.data });
    });
    this.getCommentsAmount()
  }
   getCommentsAmount = ()=>{
     api.getCommentsAmount().then(({data})=>{
      //  console.log(data)
       this.setState({ commentsAmount: data });

     })
  }
  render() {
    // console.log(this.state.data)
    return (
      <div className="topic">
        <Header />
        {/* <div className='cover'></div> */}
        <div className="wrapper">
          <div className="leftSide">
            {this.state.data.map((dataItem, index) => (
              <CardItem
                key={index}
                data={dataItem}
                commentsAmount={this.state.commentsAmount}
                className="cardItem"
              />
            ))}
          </div>
          <div className="rightSide">
            <div className="card">
              <div className="globalWrite-nav">
                <Icon type="edit" />
                <span>写回答</span>
              </div>
              <div className="globalWrite-nav">
                <Icon type="file-search" />
                <span>我的评论</span>
              </div>
              <div className="globalWrite-nav">
                <Icon type="smile" />
                <span>我的点赞</span>
              </div>
              <div className="globalWrite-draft">
                <Icon type="crown" />
                <span>我的分享</span>
              </div>
            </div>
            {/* <div className='IMG'>
             <img src='https://pic2.zhimg.com/21095e813ceb40a49b2e05cebf1b05fd_r.jpg'></img>
             <img src='https://pic3.zhimg.com/80/8bf482b9e8b765e1716cf6ab2e0449ba_hd.jpg'></img>
           </div> */}
          </div>
        </div>
      </div>
    );
  }
}

export default Topic;
