import * as Types from '../actionTypes';
export const test = () => {
    return {
      type: Types.TEST,
    }
  }
export const addCart = () =>{
    return {
      type: Types.CARTADDNUM
    };
} 
export const init = (num) =>{
  return{
    type:Types.INIT,
    num:num
  }
}
export const addCombo = (src) => {
  return {
    type: Types.CHANGECOMBO ,
    src:src
  }
}
  export const login = (info) => {
    return {
      type: Types.LOGIN,
      info: info
    };
}