import React from "react";
import { Button, Card } from "antd";
import "./alterCard.scss";

import * as actions from "../../store/actions/index";
import { connect } from "react-redux";
import api from "../../api";
const mapStateToProps = state => state.Cart;
export default connect(
  mapStateToProps,
  actions
)(function alterCard(props) {
  const { className, picUrl, title, id } = props;
  // console.log(props)
  const custId = localStorage.getItem('custId')
  const addShop = e => {
    console.log(props)
    api.addClothesOrders({
     'rentClothes.cloId' :id,
      'customer.custId': custId
    }).then((res)=>{
      console.log(res)
    })
    console.log(props);
    props.addCart();
  };
  return (
    <div>
      <Card
        style={{ width: "220px", height: "300px" }}
        className={className}
        hoverable
        cover={
          <img
            alt="example"
            style={{height:'280px'}}
            src={require("../../images/" + picUrl)}
          />
        }
      >
        {/* <Card.Meta title="Europe Street beat" description={title} /> */}
        <Button type="primary" onClick={addShop}>
         立即预订
        </Button>
      </Card>
    </div>
  );
});
