import React, { Component } from "react";
import { Input, Icon } from "antd";
import { NavLink } from "react-router-dom";
import "./head.scss";

import * as actions from "../../store/actions/index";
import { connect } from "react-redux";

const Search = Input.Search;
const userName = localStorage.getItem("userName");
class Login extends Component {
  render() {
    console.log(userName)
    return (
      <div className="header">
        <NavLink to="/home">
          <img src="https://www.hermes.cn/sites/all/themes/custom/hermes/img/hermes-logo.svg" />
        </NavLink>
        <NavLink to="/rent">
          <span>
            <Icon type="shopping" />
          </span>
          <span>服装租赁</span>
        </NavLink>
        <NavLink to="/store">
          <span>
            <Icon type="shopping" />
          </span>
          <span>摄影拍照</span>
        </NavLink>
        <NavLink to="/appreciation">
          <span>
            <Icon type="bars" />
          </span>
          <span>客照欣赏</span>
        </NavLink>
        <NavLink to="/topic">
          <span>
            <Icon type="bars" />
          </span>
          <span>话题讨论</span>
        </NavLink>
        <NavLink to="/cart">
          <span>
            <Icon type="shopping" />
            {this.props.count ? <div className = "cartNum">{this.props.count}</div>:<></>}
            
          </span>
          <span>个人中心</span>
        </NavLink>

        {!userName ? (
          <NavLink to="/signin" className="login">
            <span>
              <Icon type="user" />
            </span>
            <span>登陆/注册</span>
          </NavLink>
        ) : (
            <a className="exit"
            onClick={() => {
              localStorage.removeItem("userName");
              // console.log(this.props);
              window.location.reload();
              console.log(userName);
            }}
          >
            <span>{userName}</span>
            <span>
              <Icon type="user"  onClick={() => {}} />|退出
            </span>
          </a>
        )}
      </div>
    );
  }
}
const mapStateToProps = state => state.Cart;
export default connect(
  mapStateToProps,
  actions
)(Login);
