import React, { Component } from "react";
import { Button, Card } from "antd";
import Header from "../../components/head/head";
import api from "../../api/index";
import AlterCard from "../../components/AlterCard/alterCard";
import "./rent.scss";
const { Meta } = Card;
const imgBaseUrl = "../../images/";

// import Category1 from "../../imgs/category1.jpg";

const list1 = { title: "种类", item: ["礼服", "婚纱", "汉服", "cosplay"] };
const list2 = {
  title: "价格",
  item: ["小于300", "小于500", "小于800", "小于1000", "大于1000"]
};
const list3 = {
  title: "细节",
  item: ["口袋", "拼接", "蕾丝", "镂空", "纱网", "不对称"]
};

const obj1 = {
  礼服: 1,
  婚纱: 2,
  汉服: 3,
  cosplay: 4,
};
const obj2={
  小于300: 300,
  小于500: 500,
  小于800: 800,
  小于1000: 1000,
  大于1000: 1200
};
class Rent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      cloType: "",
      price: "",
      keyword: "",
      rentList: [],
      index1: -1,
      index2: -1,
      index3: -1
    };
  }
  componentDidMount() {
    this.fetchData();
  }
  fetchData = (params = {}) => {
    api.getAllRentList(params).then(({ data }) => {
      this.setState({ rentList: data });
    });
  };
  render() {
    let show = false;
    return (
      <div className="rent">
        <Header />
        <div className="selectList">
          <div className="line">
            <Button type="primary" className="title">
              {list1.title}
            </Button>
            <span className="options">
              {list1.item.map((ele, index) => (
                <div
              
                  className={this.state.index1==index ?'selItem':''}
                  key={index}
                  onClick={() => {
                    this.setState({index1:index})
                      this.setState({
                        cloType: obj1[ele]
                      });
                    this.fetchData({
                      price: this.state.price,
                      keyword: this.state.keyword,
                      cloType: obj1[ele]
                    });
                  }}
                >
                  {ele}
                </div>
              ))}
            </span>
          </div>
          <div className="line">
            <Button type="primary" className="title">
              {list2.title}
            </Button>
            <span className="options">
              {list2.item.map((ele, index) => (
                <div
                  className={this.state.index2 == index ? 'selItem' : ''}
                  key={index}
                  onClick={() => {
                    this.setState({ index2: index })

                    this.setState({
                        price: obj2[ele]
                      });
                    this.fetchData({
                      price: obj2[ele],
                      keyword: this.state.keyword,
                      cloType: this.state.cloType
                    });
                  }}
                >
                  {ele}
                </div>
              ))}
            </span>
          </div>
          <div className="line">
            <Button type="primary" className="title">
              {list3.title}
            </Button>
            <span className="options">
              {list3.item.map((ele, index) => (
                // <a onClick={this.handleSubmit()}>{ele}</a>
                <div
                  className={this.state.index3 == index ? 'selItem' : ''}
                  key={index}
                  onClick={() => {
                    this.setState({ index3: index })

                    this.setState({
                        keyword: ele
                      });
                    this.fetchData({
                      price: this.state.price,
                      keyword: ele,
                      cloType: this.state.cloType
                    });
                  }}
                >
                  {ele}
                </div>
              ))}
            </span>
          </div>
        </div>
        <div className="content">
          {this.state.rentList.map((item, id) => (
            <div className="boxItem">
              <div className="card">
                <img src={require("../../images/" + item.cloPicture)} />
                <div className="info">
                  <div className="title">{item.cloName}</div>
                </div>
              </div>
              <AlterCard
                id={id}
                className="alterCard"
                picUrl={item.cloPicture}
                title={item.title}
              />
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default Rent;
