import * as Types from "../actionTypes";

const initState = {
    comboList:[],
    count:0,
    loading: false
};

export default (state = initState, action) => {
    const newState = JSON.parse(JSON.stringify(state));

    switch (action.type) {
        case Types.CHANGECOMBO: {
            newState.comboList = action.src
        // console.log(action.src)
        return newState;
      }
      case Types.LOADING: {
        newState.loading = !newState.loading;
        return newState;
      }
      default:
        return state;
    }
};
