import React from "react";
import "./commentItem.scss";

export default function CommentItem(props) {
  if (!props.is) {
    return null;
  }
  const data = props.comments;
  console.log(data.aid);
  return (
    <div className="comWrapper">
      <h3>{props.commentsAmount}条数据</h3>
      <div className="content">
        {data.map(ele => (
          <div className="commentItem">
            <header>
              <img src={require("../../images/" + ele.customer.headerPic)} />
              <span>{ele.customer.custName}</span>
            </header>
            <p>{ele.comContent}</p>
          </div>
        ))}
      </div>
      <div className="input">
        <input placeholder="写下你的评论..." />
        <button>发布</button>
      </div>
    </div>
  );
}
