import * as Types from "../actionTypes";

const initState = {
  count: 0,
  loading:false
};

export default (state = initState, action) => {
  const newState = JSON.parse(JSON.stringify(state));

  switch (action.type) {
    case Types.CARTADDNUM: {
      newState.count++;
      return newState;
    }
    case Types.INIT:{
      newState.count=action.num;
      return newState;
    }
    default:
      return state;
  }
};
