import React ,{useState}from "react";
import {
    withRouter
} from "react-router-dom"
 function cartBall(props) {
   console.log(props)
    const [flag,setFlag] = useState(false)
    return (
    <div  style={{  
    position:'absolute',
    right:'20px'
    }}>
     <div 
     style={{width:'20px',
             height:'20px',
             borderRadius:'50%',
             background:'#efefef',
             boxShadow: '0px 0px 20px 5px #bcbcbc',
             position:'absolute',
             right:'20px'
            }}
    onMouseOver={()=>{
        setFlag(true)
        console.log(flag)
    }}  
    onMouseOut={()=>{
        setFlag(false)
        console.log(flag)
    }} 
    onClick={()=>{
        props.history.push('/cart')
    }}>

     </div>
     <div
    
    style={flag?{width:'150px',
                 height:'50px',
                 border:'1px solid #eee',
                 lineHeight:'50px',
                 position:'absolute',
                 right:'0px',
                 top:'40px',
                 visibility:'visible'}:{visibility:'hidden'}}
     >点击进入购物车</div>
     </div>
    )
}
export default withRouter(cartBall)