import React, { Component } from "react";
import { Table, Select, Divider, Tag, Button } from "antd";
import UserInfo from "./userInfo";

import * as actions from "../../store/actions/index";
import { connect } from "react-redux";

import Header from "../../components/head/head";
// import {getCartList} from '../../api'
import api from "../../api";
import "./shop.scss";
const custId = localStorage.getItem("custId");
const Option = Select.Option;
class Shop extends Component {
  state = {
    cartList: [],
    comboList: [],
    count: 0
  };

  componentDidMount() {
    console.log(this.props)
    const custId = localStorage.getItem("custId");
    api.getComboOrderWithCustId(custId).then(res => {
      this.setState({ comboList: res.data });
      console.log(this.state.comboList);
    });
    api.getClothesOrdersWithCustId(custId).then(res => {
      this.setState({ cartList: res.data });
      console.log(res.data);
      const len= res.data.length;
      this.props.init(len)
    });
  }
  getCart = () => {
    const { getCartList } = api;
    getCartList().then(({ data }) => {
      this.setState({ cartList: data.data.list, count: data.data.count });
    });
  };
  postNum = () => {
    const { getCartList } = api;
  };
  handleChangNum = (val, option) => {
    // console.log(val,option)
    this.getCart();
  };
  exit = () => {
    this.props.history.push("/home");
  };
  render() {
    const comboColumns = [
      {
        title: "订单编号",
        dataIndex: "comOrderId",
        key: "comOrderId"
      },
      {
        title: "套餐名称",
        dataIndex: "combo",
        key: "combo",
        render: text => <span>{text.coName}</span>
      },
      {
        title: "金额",
        dataIndex: "price",
        key: "price"
      },
      {
        title: "顾客信息",
        dataIndex: "customer",
        key: "customer",
        render: text => <span>{text.custName}</span>
      },
      {
        title: "订单时间",
        dataIndex: "comOderDate",
        key: "comOderDate"
      },
      {
        title: "拍摄时间开始时间",
        dataIndex: "startDate",
        key: "startDate"
      },
      {
        title: "拍摄地点",
        dataIndex: "shootingLocation",
        key: "shootingLocation",
        render: text => <span>{text.lname}</span>
      },
      {
        title: "拍摄状态",
        dataIndex: "comboOrderState",
        key: "comboOrderState",
        render: text => <span>{text ? text.osState : "无状态"}</span>
        // console.log(text.osState)
      }
    ];
    const columns = [
      {
        title: "衣服名称",
        dataIndex: "rentClothes",
        key: "rentClothes",
        render: text => (text ? <span>{text.cloName}</span> : <span />)
      },
      {
        title: "价格",
        key: "price",
        dataIndex: "price",
        render: (text, render) => (
          <span>
            {render.rentClothes ? render.rentClothes.cloPrice : ""}
          </span>
        )
      },
      // {
      //   title: "总计",
      //   key: "totalPrice",
      //   dataIndex: "totalPrice"
      // },
      {
        title: "订单状态",
        key: "clothesOrderState",
        dataIndex: "clothesOrderState",
        render: text => <div >{text.cosName}</div>
      }
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          "selectedRows: ",
          selectedRows
        );
      },
      getCheckboxProps: record => ({
        disabled: record.name === "Disabled User", // Column configuration not to be checked
        name: record.name
      })
    };
    return (
      <section className="shopCar">
        <Header />
        <section className="content">
          <header>
            <span
              className="goShop"
              onClick={() => {
                this.props.history.push("/home");
              }}
            >
              &lt;继续购物
            </span>

            {/* <div
              onClick={() => {
                console.log(this.props);
                // actions.test()
                this.props.test()
              }}
            >
              共件商品{this.props.count}
            </div> */}

            {/* <span>我的购物袋({this.state.count})</span> */}
          </header>
          <UserInfo />
          <div className="box">
            <section className="table">
              <p>租赁商品</p>
              <Table
                rowSelection={rowSelection}
                columns={columns}
                dataSource={this.state.cartList}
                pagination={false}
              />
            </section>
            <section className="table">
              <p>预约套餐</p>
              <Table
                rowSelection={rowSelection}
                columns={comboColumns}
                dataSource={this.state.comboList}
                pagination={false}
              />
            </section>
            <section className="account">
              <div>订单总结</div>
              <Button className="jiesuan">立即结算</Button>
              {/* <span>商品价格:￥4444</span>
              <span>总价￥4444</span> */}
            </section>
            <section className="exit">
              <Button type="danger" onClick={this.exit}>
                退出登录
              </Button>
            </section>
          </div>
        </section>
      </section>
    );
  }
}
const mapStateToProps = state => state.Test;
export default connect(
  mapStateToProps,
  actions
)(Shop);
