import React, { Component } from "react";
import api from "../../api/index";
import { Link } from "react-router-dom";
import { Button, Modal, Select,Input ,message} from "antd";
import ReactCompareImage from "react-compare-image";
import "./packageDet.scss";
import * as actions from "../../store/actions/index";
import { connect } from "react-redux";
const { Option } = Select;
const mapStateToProps = state => (state.ChangeCombo);
// const mapStateToProps2 = state => state.Login;
export default connect(
  mapStateToProps,
  actions
)(
  class packageDet extends Component {
    state = {
      imgList: [],
      visible: false,
      detailImgs: [],
      dateList: [],
      comboList: this.props.comboList,
      lid: null,
      price: null, 
      startDate:'',
      loginInfo:{},
      custName: localStorage.getItem(
        "userName",
      )
    };
    


    // onChange=(value)=> {
    //     console.log(value);
    //   }

    handleData = () => {
      const { comboList } = this.props;
      const arr = [];

      for (let [key, value] of Object.entries(comboList)) {
        if (key.includes("detail")) {
          arr.push(value);
        }
      }
      return arr;
    };
    showModal = () => {
      this.setState({
        visible: true
      });
    };

    handleOk = e => {
      const custId=localStorage.getItem("custId");
      console.log(custId);
      this.setState({
        visible: false
      });
      api
        .addComboOrders(
          `${this.state.comboList.coId}/${custId}/${this.state.lid}/${
            this.state.price
        }/${this.state.startDate}`
        )
        .then(res => {
          console.log(res);
          message.info('预约成功');
        });
    };

    handleCancel = e => {
      this.setState({
        visible: false
      });
    };

    selectHandleChange = item => {
      this.setState({
        lid: item
      });
      api.getDatesWithId(item).then(res => {
        this.setState({
          dateList: res.data
        });
      });
      // console.log(item, this.state.lid);
      for (let [key, val] of Object.entries(
        this.state.comboList.shootingLocations
      )) {
        if (val.lid == item) {
          this.setState({
            price: val.comboLocation.comboPrice
          });
        }
      }
    };
    selectDataChange = item => {
    console.log(item)
    };
    render() {
      const { comboList } = this.props;
      console.log(this.state.custName);
      const option = comboList.shootingLocations || [1, 2, 3];
      const data = this.handleData();
      console.log(this.state.comboList.coName);
      return (
        <div
          className="packageDet"
          onClick={() => {
            console.log();
          }}
        >
          <Link to="/store" className="goMore">
            &lt;继续逛逛
          </Link>

          <div className="picShow">
            {data.map(ele => (
              <img src={require(`../../images/${ele}`)} />
            ))}
          </div>
          <div className="order">
            <Button onClick={this.showModal}>点击加入套餐</Button>
          </div>
          <Modal
            title="提交订单"
            okText="去拍摄"
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
            <span>用户名</span>
            <Input
              disabled
              placeholder={this.state.custName}
              style={{ height: "50px", margin: "20px 0" }}
            />
            <span>套餐名称</span>
            <Input
              disabled
              placeholder={this.state.comboList.coName}
              style={{ height: "50px", margin: "20px 0" }}
            />

            <Select
              // defaultValue=""
              placeholder='选择地点'
              style={{ width: 120 }}
              onChange={this.selectHandleChange}
              style={{ width: "300px", height: "80px", margin: "20px 0" }}
            >
              {option.map((item, index) => (
                <Select.Option key={index} value={item.lid}>
                  {item.lname}
                </Select.Option>
              ))}
            </Select>
            <Select
              placeholder='选择时间'
              style={{ width: 120 }}
              onChange={val => {
                this.setState({
                  startDate: val
                });
              }}
            >
              {this.state.dateList.map((item, index) => (
                <Select.Option key={index} value={item}>
                  {item}
                </Select.Option>
              ))}
            </Select>
            <Button
              shape="round"
              style={{ width: "200px", height: "60px", display: "block" }}
            >
              {this.state.price
                ? `套餐价格:${this.state.price}元`
                : "价格"}
            </Button>
          </Modal>
        </div>
      );
    }
  }
);
