import React from "react";
import { Tabs } from "antd";
import "./picCon.scss";
import { Link, withRouter } from "react-router-dom";
import { object } from "prop-types";

import * as actions from "../../../store/actions/index";
import { connect } from "react-redux";

const TabPane = Tabs.TabPane;

const mapStateToProps = state => state.ChangeCombo;
export default connect(
  mapStateToProps,
  actions
)(function picCon(props) {
  const { src } = props;
  console.log(src);
  // this.props.addCombo();
  console.log(props.count);
  return (
    <section className="storeContent">
      {src.map((ele, index) => (
        <div>
          <Link to={`/store/${ele[0].coName}/${index}`}>
            <section
              className="line1 "
              onClick={() => {
                props.addCombo(ele[0]);
              }}
            >
              <span className="leftImg">
                <img src={require("../../../images/" + ele[0].coPicture)} />
              </span>
              <span className="rightText">
                <p>{ele[0].coName}</p>
                <p className='exindehua'>
                  你我相逢在黑夜的海上，
                  <br /> 你有你的，我有我的，方向；
                  <br />
                  你记得也好，最好你忘掉，<br /> 在这交会时互放的光亮！
                </p>
              </span>
            </section>
          </Link>

          <section className="line2">
            {ele[1].map((item, index) => (
              <Link to={`/store/${item.coName}/${index + 1}`}>
                <div
                  className="box"
                  onClick={() => {
                    props.addCombo(item);
                  }}
                >
                  <span className="TopImg">
                    <img src={require("../../../images/" + item.coPicture)} />
                  </span>
                  <div className="BotText">
                    <p>{item.coName}</p>
                    {/* <h3>{item.coName}</h3> */}
                  </div>
                </div>
              </Link>
            ))}
          </section>
        </div>
      ))}
    </section>
  );
});
