import React, { Component } from "react";
import { Route, Switch, Link } from "react-router-dom";
import { Tabs } from "antd";
import PicCon from "./picCon/picCon";
import Header from "../../components/head/head";
import api from "../../api/index";
import CardItem from "../../components/cardItem/cardItem";
import "./store.scss";
import storeCom from "../../components/storeCom/storeCom";
import * as actions from "../../store/actions/index";
import { connect } from "react-redux";
const TabPane = Tabs.TabPane;
class Store extends Component {
  state = {
    activeKey: "",
    storeData: []
  };
  componentWillMount = () => {
    // console.log(this.props);
    const toId = localStorage.getItem("toId");
    // console.log(toId)
    const key = toId.slice(2, 3).toString();
    this.setState({ activeKey: key });
    this.fetchData(key);
  };
  fetchData = activeKey => {
    // api.getAllCombo(activeKey).then(({ data }) => {
    api.getAllCombo(activeKey).then(({ data }) => {
      // console.log(data)
      this.dataInit(data);
    });
  };
  dataInit = res => {
    const data = [];
    const data2 = [];
    const src = [1, 2, 3,4,5,6,7];
    console.log(res)
    for (var i = 0, j = res.length; i < j; i += 3) {
      var arr = res.slice(i, i + 3);
      data.push(arr);
    }
    // console.log(data)
    data.forEach(ele => {
      const arr2 = ele.splice(1, 3);
      ele = [ele[0], [...arr2]];
      data2.push(ele);
    });
    this.setState({ storeData: data2 });
  };
  tabChange = activeKey => {
    this.setState({
      activeKey: activeKey
    });
    this.fetchData(activeKey);
 
  };
  render() {
    // console.log(this.state.storeData);
    return (
      <div className="store">
        <Header />
        <Tabs
          className="content"
          onChange={this.tabChange}
          defaultActiveKey={this.state.activeKey}
        >
          <TabPane tab="婚纱摄影" key="1">
            <PicCon src={this.state.storeData} />
          </TabPane>
          <TabPane tab="古风特写" key="2">
            <PicCon src={this.state.storeData} />
          </TabPane>
          <TabPane tab="个人写真" key="3">
            <PicCon src={this.state.storeData} />
          </TabPane>

          <TabPane tab="情侣写真" key="4">
            <PicCon src={this.state.storeData} />
          </TabPane>
          <TabPane tab="童年记忆" key="5">
            <PicCon src={this.state.storeData} />
          </TabPane>
          <TabPane tab="毕业季" key="6">
            <PicCon src={this.state.storeData} />
          </TabPane>
          <TabPane tab="亲子照" key="7">
            <PicCon src={this.state.storeData} />
          </TabPane>
          <TabPane tab="cosplay" key="8">
            <PicCon src={this.state.storeData} />
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

const mapStateToProps = state => state.ChangeCombo;
export default connect(
  mapStateToProps,
  actions
)(Store);
