import React, { Component } from "react";
import { Input, Button, Select, Divider, message } from "antd";
// import PropTypes from 'prop-types';
import api from "../../api/index";
import "./login.scss";
import * as actions from "../../store/actions/index";
import { connect } from "react-redux";
const Option = Select.Option;
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      a: "sign",
      custPhone: "",
      password: ""
    };
  }
  handleSubmit = () => {
    const url = this.state.a == "sign" ? "signUser" : "postUser";
 
    api[url]({
      custPhone: this.state.custPhone,
      password: this.state.password
    }).then(({ data }) => {
      console.log(data);
      if (data.customer) {
        if (data.customer.custName) {
          localStorage.setItem("userName", data.customer.custName);
          localStorage.setItem("custId", data.customer.custId);
        } else {
          localStorage.setItem("userName", data.customer.custPhone);
        }
      }else{
        message.error("This is a normal message");
      }

      // console.log(this.props)
      this.props.login(data);
      // console.log(this.props.)
      this.props.history.push("/home");
    });
  };
  render() {
    return (
      <div className="login">
        <div className="cover" />
        <div
          className="card"
          key={this.state.a == "sign" ? "sign" : "login"}
        >
          <div className="title">
            <div>单椒煜泽</div>
            <div>
              {this.state.a == "sign" ? "注册" : "登录"}
              单椒煜泽，留住时光
            </div>
          </div>

          <div className="table">
            <div className="inputItem">
              {this.state.a == "sign" ? (
                <div>
                  <Select defaultValue="jack" className="select">
                    <Option value="jack">中国+86</Option>
                    <Option value="lucy">香港+856</Option>
                    <Option value="disabled">美国+1</Option>
                    <Option value="Yiminghe">日本+81</Option>
                  </Select>
                  <Divider type="vertical" />
                </div>
              ) : (
                <div />
              )}

              <Input
                size="large"
                placeholder={
                  this.state.a == "sign" ? "手机号" : "手机号或邮箱"
                }
                value={this.state.custPhone}
                onChange={e => {
                  this.setState({
                    custPhone: e.target.value
                  });
                }}
              />
            </div>
            <div className="inputItem">
              <Input.Password
                size="large"
                placeholder={this.state.a == "sign" ? "请输入密码" : "密码"}
                value={this.state.password}
                onChange={e => {
                  this.setState({ password: e.target.value });
                }}
              />
            </div>
            {/* <div className='inputItem'>
            <Input size="large" 
                   placeholder={this.state.a=='sign'?'请输入密码':'密码'} 
                   value={this.state.passVal}
                   onChange={(e)=>{this.setState({passVal:e.target.value})}}/>
            </div> */}
            <Button type="primary" onClick={this.handleSubmit}>
              {this.state.a == "sign" ? "注册" : "登录"}
            </Button>
          </div>
          <div className="text">
            {this.state.a == "sign"
              ? "注册即代表同意《协议》《隐私保护指引》"
              : "二维码登录"}
          </div>
          <div className="footer">
            {this.state.a == "sign" ? (
              <div>
                <span>已有帐号？</span>
                <span
                  onClick={() => {
                    this.setState({ a: "login" });
                  }}
                >
                  登录
                </span>
              </div>
            ) : (
              <div>
                <span>没有帐号？</span>
                <span
                  onClick={() => {
                    this.setState({ a: "sign" });
                  }}
                >
                  注册
                </span>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => state.Login;
export default connect(
  mapStateToProps,
  actions
)(Login);
