import React, { Component } from "react";
import {
  Upload,
  Icon,
  message,
  Avatar,
  Modal,
  Input,
  Button
} from "antd";
import "./userInfo.scss";
// import { Button } from "antd/lib/radio";
const userName = localStorage.getItem("userName");
function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener("load", () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file) {
  const isJPG = file.type === "image/jpeg";
  if (!isJPG) {
    message.error("You can only upload JPG file!");
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error("Image must smaller than 2MB!");
  }
  return isJPG && isLt2M;
}

class userInfo extends Component {
  state = {
    loading: false,
    imageUrl: "",
    visible: false
  };


  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    // console.log(e);
    message.info('修改成功')
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleChange = info => {
    if (info.file.status === "uploading") {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === "done") {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl =>
        this.setState({
          imageUrl,
          loading: true
        })
      );
    }
  };

  render() {
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? "loading" : "plus"} />
        <div className="ant-upload-text">点击上传头像</div>
      </div>
    );
    const props = {
      name: 'file',
      action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
      headers: {
        authorization: 'authorization-text',
      },
      onChange(info) {
        if (info.file.status !== 'uploading') {
          console.log(info.file, info.fileList);
        }
        if (info.file.status === 'done') {
          message.success(`${info.file.name} file uploaded successfully`);
        } else if (info.file.status === 'error') {
          message.error(`${info.file.name} file upload failed.`);
        }
      },
    };
    return (
      <section className="userWrap">
        <section className="userImg">
          <Upload
            name="avatar"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            beforeUpload={beforeUpload}
            onChange={this.handleChange}
          >
            {this.state.imageUrl ? (
              <Avatar src={this.state.imageUrl} size={120} />
            ) : (
              uploadButton
            )}
          </Upload>
        </section>
        <div className="userName">{userName}</div>
        <Button className="changeInfo" onClick={this.showModal}>
          修改信息
        </Button>
        <Modal
          title="修改基本信息"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          {" "}
          <Upload {...props}>
            <Button>
              <Icon type="upload" style={{ margin: "10px 0", height: '70px' }}/> 点击更改头像
            </Button>
          </Upload>
          <Input placeholder="修改用户名" style={{margin:"10px 0",height:'60px'}}/>
          <Input placeholder="修改电话号码" style={{ margin: "10px 0", height: '60px'}} />
        </Modal>
      </section>
    );
  }
}

export default userInfo;
