import React, { Component } from "react";
import "./App.css";
import { Route, Switch, Redirect } from "react-router-dom";
import Login from "./router/login/login";
import Home from "./router/home/home";
import Topic from "./router/topic/topic";
import Store from "./router/store/store";
import Rent from "./router/rent/rent";
import Cart from "./router/shop/shop";
import Appreciation from "./router/appreciation/appreciation";
import PackageDet from "./router/packageDet/packageDet";
class App extends Component {
  render() {
    return (
      <div className="App">
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/home" exact component={Home} />
          <Route path="/store" exact component={Store} />
          <Route path="/store/:type/:id" component={PackageDet} />
          <Route path="/topic" component={Topic} />
          <Route path="/shoot" component={Topic} />
          <Route path="/signin" component={Login} />
          <Route path="/rent" component={Rent} />
          <Route path="/cart" component={Cart} />
          <Route path="/appreciation" component={Appreciation} />
        </Switch>
      </div>
    );
  }
}

export default App;
